import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

//import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Row, Col, Select, Spin, Divider } from "antd";
import actions from "../../../../redux/generalLedger/tax/actions";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
// const { TextArea } = Input;
// const InputGroup = Input.Group;

class GeneralLedgerTaxForm extends Component {

  handleChangeTax = (value) => {
    const {setFieldsValue} = this.props.form 
    this.props.generalLedgerTax.taxes.map(data => {
      if(data.id_tax === value){
        setFieldsValue({
          ["id_account_sale"]: data.id_account_sale,
          ["id_account_purchase"]: data.id_account_purchase,
        }) 
      }
          
    })
  }

  handleSubmit = e => {
    //console.log("Masuk Pak")
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, value) => {
      if (!err) {
        const { updateGLTax } = this.props;
        // console.log(values);
        updateGLTax(value.id_tax, value);
      }
    });
  };


  render() {
    // console.log(this.props.generalLedgerTax);
    const { getFieldDecorator } = this.props.form;
    const { generalLedgerTax } = this.props;
    const { loading, loadingSubmit } = generalLedgerTax;
    const data = generalLedgerTax.taxes ? generalLedgerTax.taxes : [];

    // const used = data.is_used;
    // const error = generalLedgerTax.errorField ? generalLedgerTax.errorField : [];
    // let opening_balance=null;
    // let retained_earning=null;


    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <div>
        {this.props.generalLedgerTax.loading  ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (

      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="taxes.pageHeader" />}
            >
              {getFieldDecorator("id_tax", {
                // initialValue: ,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={generalLedgerTax.loading}
                  showSearch
                  optionFilterProp="children"
                  onChange={this.handleChangeTax}
                  placeholder={getLanguage({ id: "noTax"})}
                >
                  {this.props.generalLedgerTax.taxes.map((data, key) => (
                    <Option key={key} value={data.id_tax}>
                      {data.tax_name}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </div>
        </div>

        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="taxes.add.subtitle.accounts" />
        </Divider>

        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
          <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="taxes.add.salesAccount" />}
            >
              {getFieldDecorator("id_account_sale", {
                initialValue: data.id_account_sale,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={generalLedgerTax.loading}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.generalLedgerTax.accounts.map((data, key) => (
                    data.id_account_type === "10"
                    ? (<Option key={key} value={data.id_account}>
                        {data.account_code + " (" + data.account_name + ")"}
                      </Option>)
                    : null
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="taxes.add.purchaseAccount" />}
            >
              {getFieldDecorator("id_account_purchase", {
                initialValue: data.id_account_purchase,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={generalLedgerTax.loading}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.generalLedgerTax.accounts.map((data, key) => (
                    data.id_account_type === "05"
                    ? (<Option key={key} value={data.id_account}>
                        {data.account_code + " (" + data.account_name + ")"}
                      </Option>)
                    : null
                  ))}
                </Select>
              )}
            </Form.Item>
          </div>
        </div>

        <Row>
          <Col>
            <Form.Item {...tailFormItemLayout}>
              <Button
                type="primary"
                htmlType="submit"
                style={{ marginTop: "20px", marginLeft: "50px" }}
                loading={loading || loadingSubmit}
              >
                <IntlMessages id="button.submit" />
              </Button>
            </Form.Item>
          </Col>
        </Row>

        
      </Form>
      )}
      </div>
    );
  }
}

const WrappedGeneralLedgerTaxForm = Form.create({
  name: "general_ledger_tax"
})(GeneralLedgerTaxForm);

//export default WrappedGeneralLedgerTaxForm;

const mapStateToProps = state => {
  return { generalLedgerTax: state.generalLedgerTax };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(WrappedGeneralLedgerTaxForm);
