const actions = {
  getGLTaxes: () => ({
    type: 'GET_GL_TAXES_REQUEST',
  }),
  updateGLTax: (id_tax, data) => ({
    type: 'UPDATE_GL_TAX_REQUEST',
    payload: {
      id_tax: id_tax,
      body: data
    }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};


export default actions;
