import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import TaxForm from "./updateTaxForm";

import { Tabs, Divider } from "antd";

import notification from "../../../../components/notification";
import Button from "../../../../components/uielements/button";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Box } from "../../../App/app.style";
import actions from "../../../../redux/generalLedger/tax/actions";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/setup/menus";

class UpdateGeneralLedger extends Component {
  componentDidMount = () => {
    const { getGLTaxes } = this.props;
    getGLTaxes();
  };

  componentDidUpdate = () => {
    //Display error notif
    if (this.props.generalLedgerTax.error) {
      notification("error", this.props.generalLedgerTax.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.generalLedgerTax.success) {
      notification("success", this.props.generalLedgerTax.success);
      this.props.clearSuccess();
    }
  };

  render() {
    //const general_ledger = this.props.tax.tax ? this.props.tax.tax : []
    // console.log(this.props.generalLedgerTax);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="generalLedgers.tax.title" />}
            style={{ margin: 0 }}
          >
            <Button
              onClick={() => this.props.history.replace(redirectTo)}
              style={{ marginLeft: "5px" }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>
          <TaxForm />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { generalLedgerTax: state.generalLedgerTax };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateGeneralLedger);
